
.linkExternal {
  white-space:   nowrap;
  border-bottom: 1px solid var(--text-color-gunmetal);
  cursor:        pointer;
  margin-right:  2px;
}

.linkExternal:hover {
  border-bottom: 2px solid var(--text-color-gunmetal);
  font-weight:   600;
  transition:    border-bottom .15s;
}

.imprintRedirectIcon {
  position: relative;
  bottom:   2px;
}
